.container {
    width: 700px;
    margin: 16px auto 0 auto;
    border-radius: 0;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.bottom {
position: absolute;
bottom: 16px;
width: 700px;
border: 1px solid #AAAAAA;
display: flex;
    padding: 5px;
}

@media (max-width: 700px) {
    .container {
        width: unset;
    }
    .bottom {
        width: unset;
    }
}